body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.input-label {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  margin-bottom: 30px;
}

.input-field {
  width: 100%;
  padding: 10px;
  font-size: 20px;
  border: none;
  border-bottom: 2px solid gray;
  margin-top: 10px;
  margin-bottom: 20px;
}

.submit-button {
  background-color: #008CBA;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  font-size: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.error-message {
  color: red;
  font-size: 18px;
  margin-top: 20px;
}

.output-text {
  font-size: 18px;
  margin-top: 20px;
}
